@font-face {font-family: "Century Gothic";
    src: url("centurygothic.eot"); /* IE9*/
    src: url("centurygothic.eot?#iefix") format("embedded-opentype"), /* IE6-IE8 */
    url("centurygothic.woff2") format("woff2"), /* chrome、firefox */
    url("centurygothic.woff") format("woff"), /* chrome、firefox */
    url("centurygothic.ttf") format("truetype"), /* chrome、firefox、opera、Safari, Android, iOS 4.2+*/
    url("centurygothic.svg#Century Gothic") format("svg"); /* iOS 4.1- */
  }